// Text-related behaviors

(function (c2, $) {
  'use strict';


  function enableDoubleClickSelection(container) {
    $(container).on('dblclick', '[data-dblclickable]', function(e) {
      e.preventDefault();
      $(this).selectText();
    });
  }


  c2.text = {
    enableDoubleClickSelection: enableDoubleClickSelection
  };
})(window.c2, window.jQuery);
